<template>
  <div class="circle1" v-bind:style="[CircleColor]"></div>
  <div class="circle2" v-bind:style="[CircleColor]"></div>
  <div class="circle3" v-bind:style="[CircleColor]"></div>
</template>

<script>
export default {
  name: "AppBackground",
  computed: {
    currentRouteName() {
      return this.$route.name;
    },
    Home() {
      if (this.currentRouteName == "home") {
        return true;
      } else {
        return false;
      }
    },
    About() {
      if (this.currentRouteName == "about") {
        return true;
      } else {
        return false;
      }
    },
    Projects() {
      if (this.currentRouteName == "projects") {
        return true;
      } else {
        return false;
      }
    },
    Blogs() {
      if (this.currentRouteName == "blogs") {
        return true;
      } else {
        return false;
      }
    },
    Contacts() {
      if (this.currentRouteName == "contacts") {
        return true;
      } else {
        return false;
      }
    },
    CircleColor() {
      if (this.About) {
        return "background: #ffd0b670";
      } else if (this.Projects) {
        return "background: #bdffc470";
      } else if (this.Blogs) {
        return "background: #bdfbff70";
      } else if (this.Contacts) {
        return "background: #ffbdbd70";
      } else {
        return "background: #BDD0FF70";
      }
    },
  },
};
</script>
